.footer {
  height: 540px;
  position: relative;
  background-image: url("/images/footer.jpg");
  background-size: cover;
  background-position: center;

  &__logo {
    background-image: url(/images/logo.png);
    background-size: cover;
    height: 76px;
    width: 115px;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 95px;
  }
}

